.Sidedrawer {
    width: 260px;
    max-width: 100%;
    height: 100%;
    background-color: rgb(44,54,68) !important;
    padding: 10px 15px;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-right: -260px;
}

.Sidedrawer_Open {
    -webkit-animation: sidedrawer_show 0.3s ease-out forwards;
            animation: sidedrawer_show 0.3s ease-out forwards;
}

@-webkit-keyframes sidedrawer_show {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        margin-right: -260px;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin-right: 0px;
    }
}

@keyframes sidedrawer_show {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        margin-right: -260px;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin-right: 0px;
    }
}

.Sidedrawer_Close {
    -webkit-animation: sidedrawer_close 0.33s ease-in forwards;
            animation: sidedrawer_close 0.33s ease-in forwards;
}

@-webkit-keyframes sidedrawer_close {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin-right: 0;
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        margin-right: -260px;
    }
}

@keyframes sidedrawer_close {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin-right: 0;
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        margin-right: -260px;
    }
}

